.swiper-container {
  width: 80vh;
  height: 80vh;
  @include tabletM {
    width: 50vh;
    height: 50vh;
  }
}
.swiper-slide {
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 18px;
  background: $white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-button-next, .swiper-button-prev {
  color: $black;
  &:hover {
    color: $accent;
  }
}