.about-main {
  padding-top: 13vh;
  display: flex;
  justify-content: flex-start;
  @include tablet {
    flex-direction: column;
  }
  &__img-wrapper {
    width: 45%;
    display: flex;
    align-items: center;
    margin-right: 10vw;
    @include tablet {
      width: 90%;
      margin: 0 auto 10vh auto;
    }
  }
  &__img {
    object-fit: cover;
  }
}

.about-article {
  position: relative;
  width: 50%;
  display: flex;
  align-items: center;
  color: $black;
  @include tablet {
    width: 100%;
  }
  &__container {
    font-size: 2rem;
    @include laptopL {
      font-size: 1.2rem;
    }
  }
  &__sub-title {
    margin-bottom: 30px;
  }
  &__descr {
    margin-bottom: 50px;
  }
  &__width {
    display: inline-block;
    width: 130px;
  }
  &__link {
    position: relative;
    display: block;
    text-align: center;
    margin-bottom: 20px;
    transition: color .2s;
    border-bottom: 2px solid $black;
    @extend .hover-stripe;
    transition: letter-spacing .5s;
    &:hover, &:focus {
      letter-spacing: 2px;
    }
  }
}

.about-collab {
  &__title {
    padding: 30px;
    margin-bottom: 20px;
  }
}