.collab {
  padding: 0;
  &__title {
    font-size: 7vw;
    text-align: center;
    margin-bottom: 15vh;
  }
  &__line {
    display: flex;
    line-height: 1.1;
    justify-content: space-between;
    align-items: baseline;
    width: 100%;
    margin-bottom: 30vh;
    padding: 0 5vw;
    @include mobileL {
      margin-bottom: 35vh;
    }
    & svg {
      width: 10vh;
      height: 10vh;
      fill: $black;
      animation: svgBlink 4s ease-in-out infinite;
    }
    & svg:nth-child(2) {
      transform: translateY(50%);
      animation-delay: 2s;
    }
    & svg:nth-child(3) {
      animation-delay: 4s;
    }
  }
}

@keyframes svgBlink {
  0%, 100% {
    fill: $black;
  }
  50% {
    fill: $accent;
  }
}