.preloader {
  width: 100%;
  height: 100vh;
  color: $black;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 333;
  background: $main;
  transition: opacity 1s;
  overflow: hidden;
  &__text {
    letter-spacing: 1px;
    z-index: 3;
  }
  &__cube-container {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 200px;
    height: 200px;
    transform: translate(-50%, -50%);
    @include mobileL {
      width: 100px;
      height: 100px;
    }
  }
  &__cube {
    position: absolute;
    top: 0;
    left: 0;
    width: 200px;
    height: 200px;
    fill: $accent;
    @include mobileL {
      width: 100px;
      height: 100px;
    }
    &--1 {
      animation: rotateSquare 2s linear infinite;
    }
    &--2 {
      animation: rotateSquare 8s linear infinite;
    }
  }
}

@keyframes rotateSquare {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}