html, body, .main {
  height: 100%;
  position: relative;
  font-family: 'Overpass', serif;
  font-weight: 400;
  width: 100%;
  font-size: 16px;
  -ms-scroll-chaining: none;
  overscroll-behavior: none;
  scrollbar-width: none;
}

body {
  display: flex;
  flex-direction: column;
  height: auto;
  overflow-x: hidden!important;
  color: $white;
  background: $main;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: #FF0000;
}

a {
  text-decoration: none;
  color: $black;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

img, video {
  width: 100%;
  display: block;
  height: auto;
}

video {
  object-fit: contain;
}

.section {
  position: relative;
  padding: 7vw;
  width: 100%;
  min-height: 100vh;
}

.main {
  position: relative;
  padding: 7vw;
  width: 100%;
  min-height: 100vh;
}

.title {
  font-family: serif;
  text-align: left;
  font-size: 1.2rem;
  padding: 20px;
  text-transform: uppercase;
  z-index: 2;
  color: $black;
  @include tabletM {
    font-size: 1rem;
  }
}

.container {
  position: relative;
  flex: 1 0 auto;
}

.container-inner {
  position: relative;
  max-width: 1120px;
  margin: 0 auto;
}

.container-mini {
  max-width: 900px;
  margin: 0 auto;
}

.blur {
  filter: blur(.5px);
}

.hover-stripe {
  &:hover:after {
    left: 0;
    width: 100%;
  }
  &:after {
    content: "";
    background: $accent;
    height: 2px;
    position: absolute;
    bottom: -2px;
    right: 0;
    width: 0;
    display: block;
    transition: 1s;
  }
}

.sq-blur {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: -1;
  transform: translate(-50%, -50%);
  animation: blur 7s infinite;
  filter: blur(.25px);
  @include tabletM {
    left: 30%;
  }
  & svg {
    width: 30vh;
    height: 30vh;
    filter: blur(.5px);
  }
}

@keyframes blur {
  0% {
    transform: translate(-10%, 7%)
  }
  10% {
    transform: translate(7%, -3%)
  }
  20% {
    transform: translate(-5%, 8%)
  }
  30% {
    transform: translate(-8%, -8%)
  }
  40% {
    transform: translate(-5%, -10%)
  }
  50% {
    transform: translate(-10%, 5%)
  }
  60% {
    transform: translate(-8%, -7%)
  }
  70% {
    transform: translate(9%, -8%)
  }
  80% {
    transform: translate(-6%, -5%)
  }
  90% {
    transform: translate(6%, 9%)
  }
  to {
    transform: translate(-10%, 7%)
  }
}

.txt-border {
  animation: blink 1s infinite;
}

@keyframes blink {
  0% {opacity: 0}
  49%{opacity: 0}
  50% {opacity: 1}
}