.work {
  
}

.work-main {
  color: $black;
  display: flex;
  align-items: center;
  justify-content: center;
  &__title {
    font-size: 8.8vw;
    text-align: center;
    line-height: 1;
    font-family: serif;
    font-weight: 600;
  }
  &__footer {
    display: flex;
    width: 100%;
    padding: 0 6vw;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    bottom: 5vh;
    @include tabletM {
      flex-direction: column;
    }
  }
  &__link-back {
    height: 100%;
    font-size: 3vh;
    transition: color .2s;
    &:hover {
      color: $accent;
    }
  }
  &__text {
    @include tabletM {
      order: -3;
      margin-bottom: 40px;
    }
  }
  &__title-mini {
    font-size: 5vh;
    @include tabletM {
      display: none;
    }
  }
}

.work-article {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: transparent;
  color: $black;
  width: 100%;
  padding: 10vh;
  @include mobileL {
    padding: 5vh;
  }
  &--h {
    height: 100vh;
  }
  &--minh {
    min-height: 100vh;
    @include tablet {
      min-height: 50vh;
    }
  }
  &--maxh {
    max-height: 100vh;
  }
  &--p0 {
    padding: 0;
  }
  &--text {
    min-height: auto;
    height: auto;
  }
  &__vis-container {
    margin: 0 auto;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    &--iframe {
      padding-top: 56.25%;
      position: relative;
      & iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
    &--mini {
      width: 40%;
      height: 40%;
    }
    &--mid {
      width: 70%;
      height: 100%;
    }
    &--max {
      width: 100%;
      height: 100%;
    }
  }
  &__img {
    object-fit: cover;
    max-width: 100%;
    height: 100%;
  }
  &__video {
    z-index: 3;
    width: 100%;
  }
  &__half {
    display: flex;
    overflow: hidden;
    @include tabletL {
      flex-direction: column;
    }
    &--col {
      flex-direction: column;
      align-items: center;
      & .work-article__half-block {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }
  }
  &__half-block {
    display: flex;
    justify-content: flex-start;
    margin-right: 10vw;
    width: 50%;
    &:last-child {
      margin-right: 0;
    }
    @include tabletL {
      width: 100%;
      margin-right: 0;
      margin-bottom: 10vh;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &--center {
      align-items: center;
    }
  }
}

.work-text {
  max-width: 992px;
  margin: 0 auto;
  line-height: 5vh;
  font-size: 3vh;
  text-align: justify;
  @include laptopL {
    max-width: 600px;
  }
}

.work-footer {
  padding: 10vh 6vw;
  height: 60vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: color .2s;
  &__link {
    font-size: 5vh;
    width: 50%;
    text-align: center;
    &:hover {
      color: $accent;
    }
  }
}